  font-size: 30px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.main-about {
  justify-content: center;
  display: flex;
}

.cow-photo {
  max-width: 279px;
  width: 279px;
}

.photo-2 {
  border-radius: 140px;
  margin-top: 54px;
  box-shadow: 0 -22px #f8d24e;
}

.descrition {
  max-width: 220px;
  margin: 40px auto 20px;
}

.descrition-text {
  color: #fcfcfc;
  text-align: center;
