  line-height: 176%;
}

.about-btn {
  width: 164px;
  height: 42px;
  background: #f8d24e;
  border: none;
  border-radius: 10px;
  flex-shrink: 0;
  align-items: center;
  display: inline-flex;
}

.about-btn-text {
  color: #000;
  text-align: center;
  justify-content: center;
  align-items: center;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
}

.about-btn-svg {
  fill: #000;
}

.menu {
  background-color: var(--pastel);
  width: 100%;
  height: 100%;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  inset: 0;
  transform: translateX(100%);
}

@media screen and (min-width: 1200px) {
  .menu {
    display: none;
  }
}

.menu.is-open {
  transform: translateX(0);
}

.menu-container {
  max-width: 480px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .menu-container {
    max-width: 768px;
    padding: 0 32px;
  }
}

.menu-mob__header {
  margin-bottom: 72px;
  padding: 12px 0;
  display: flex;
}

@media screen and (min-width: 768px) {
  .menu-mob__header {
    padding: 20px 0;
  }
}

.logo {
  top: 12px;
  left: 32px;
}

.menu-mob__close {
  cursor: pointer;
  background-color: var(--pastel);
  border: none;
  margin-left: auto;
}

.menu-mob__nav {
  text-align: center;
  margin-top: 100px;
  margin-bottom: 110px;
}

.menu-mob__item:not(:last-child) {
  margin-bottom: 32px;
}

.menu-mob__link {
  color: var(--white);
  font-size: 32px;
  font-weight: 700;
}

@media screen and (min-width: 768px) {
  .menu-mob__link {
    font-size: 48px;
  }
}

.menu-mob__link:hover, .menu-mob__link:focus {
  color: var(--black);
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.menu-mob__buy {
  text-align: center;
  background-color: var(--yellow);
  border: none;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  gap: 4px;
  margin: 0 auto;
  padding: 12px 36px;
  font-size: 14px;
  font-weight: 600;
  display: flex;
}

.menu-mob__buy:hover {
  background-color: var(--form-border);
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.menu-mob__buy:focus {
  background-color: var(--form-border);
  color: var(--white);
  fill: var(--white);
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.menu-mob__buy:disabled {
  background-color: var(--grey);
}

@media screen and (min-width: 768px) {
  .menu-mob__buy {
    display: none;
  }
}

.adv-layout {
  flex-direction: column;
  align-items: center;
  gap: 16px;
  margin-bottom: 28px;
  display: flex;
}

.adv-layout:last-child {
  margin-bottom: 0;
}

.icon-background {
  color: var(--white);
  background-color: var(--yellow);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 7px;
  display: flex;
}

.adv-first-paragraph {
  color: var(--white);
  letter-spacing: 1.44px;
  text-transform: uppercase;
  font-size: 36px;
  font-style: normal;
